import DdzMainUI from "../panel/DdzMainUI";
import DdzContext from "../data/DdzContext";
import DdzDefine from "../data/DdzDefine";
import DdzPathHelper from "../data/DdzPathHelper";
import DdzDriver from "../DdzDriver";

export default class DdzBaseHandler
{    
    protected mainUI: DdzMainUI;
    protected context: DdzContext;
    protected Define: DdzDefine;
    protected SitHelper: PVPSitHelper;
    protected Path: DdzPathHelper;
    //判断协议是否需要进入队列
    public checkInQueue(msgParam)
    {
        return true;
    }
    
    //协议处理入口
    public Handle(msgParam)
    {
        this.mainUI = DdzDriver.instance.mainUI;
        this.context = DdzDriver.instance.Context;
        this.Define = DdzDriver.instance.Define;
        this.SitHelper = DdzDriver.instance.SitHelper;
        this.Path = DdzDriver.instance.Path;
        this.refreshData(msgParam);
        //重连执行executeSync 正常执行execute
        if(this.context.syncMode)
            this.executeSync(msgParam)
        else
            this.execute(msgParam);
    }
  
      //数据刷新入口
      protected refreshData(msgParam)
      {}
  
      //正常流程逻辑处理
      protected execute(msgParam)
      {}
  
      //重连流程逻辑处理
      //默认为execute  如果涉及到动画   需要重写
      protected executeSync(msgParam)
      {
          this.execute(msgParam);
      }
}